import { Axios } from 'axios'
import {
  CampaignPoint,
  CampaignPointClientModel,
  CampaignPointsSearchQuery,
  CampaignPointRoutes,
} from '../campaign-points.model'
import { ApiEntity } from '../../core'

export class CampaignPointsClient
  extends ApiEntity
  implements CampaignPointClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for search epochs
   */
  public search(searchParams?: CampaignPointsSearchQuery) {
    return this._search<CampaignPoint>(
      CampaignPointRoutes.v1Search,
      searchParams
    )
  }

  /**
   * Search all epoch by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: CampaignPointsSearchQuery) {
    return this._searchAll<CampaignPoint>(
      CampaignPointRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list epochs
   */
  public list(searchParams?: CampaignPointsSearchQuery) {
    return this._list<CampaignPoint>(CampaignPointRoutes.v1Search, searchParams)
  }

  /**
   * List all epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list epochs
   */
  public listAll(searchParams?: CampaignPointsSearchQuery) {
    return this._listAll<CampaignPoint>(
      CampaignPointRoutes.v1Search,
      searchParams
    )
  }
}
